@import './variables';

@mixin FontSize($fs-mobile, $fs-tablet, $fs-desktop) {
  font-size: $fs-mobile;

  @media screen and (min-width: $tablet) {
    font-size: $fs-tablet;
  }

  @media screen and (min-width: $desktop) {
    font-size: $fs-desktop;
  }
}

@each $name,  $color in $colors {
  .#{$name} {
    color: #{$color};
  }
}