/* media query breakpoints */
$tablet: 768px;
$laptop: 992px;
$desktop: 1200px;
$large-desktop: 1440px;

/* colors for blue theme */
$sapphire: #2752a9ff;
$celtic-blue: #3c72b8ff;
$picton-blue: #64b2d6ff;
$sky-blue: #78d1e5ff;
$non-photo-blue: #9addecff;
$azure-web: #def4f9ff;
$white: #ffffffff;

$colors: 'white' $white, 'azure-web' $azure-web, 'non-photo-blue' $non-photo-blue, 'sky-blue' $sky-blue,
  'picton-blue' $picton-blue, 'celtic-blue' $celtic-blue, 'sapphire' $sapphire;
